import http from '@/components/utils/http.js'
export default {
	data() {
		return {
			formData: {
				name:'',
				company:'',
				position:'',
				idcard:''
			},
			status:''
		}
	},
	onLoad() {
		http.apiRequest({
			api: "/api/certification/getCertification",
			method:"GET",
			data: {},
			secuss: (res, httpStatus) => {
				if (res.code == "1") {
					console.log(res)
					let data = res.data.data
					this.status = data.status
					console.log(this.status)
					this.formData.name = data.name
					this.formData.company = data.company
					this.formData.position = data.position
					this.formData.idcard = data.idcard	
				} else {
					uni.showToast({
						title: res.msg,
						duration: 1500,
						icon: "none"
					});
					// console.log("保存失败");
				}
			},
			error: (res, httpStatus) => {
				console.log("接口调用失败，请检查网络");
			},
		});
	},
	methods: {
		formSubmit: function(e) {
			http.apiRequest({
				api: "/api/certification/postCertification",
				data: this.formData,
				secuss: (res, httpStatus) => {
					if (res.code == "1") {
						uni.showToast({
							title: res.msg,
							duration: 1500
						});
						setTimeout(() => {
							//uni.reLaunch({url:"/pages/tongxun/userDetail/userDetail?id="+that.company.member.id});
							uni.navigateBack({
								delta: 1
							});
						}, 1500);
		
					} else {
						uni.showToast({
							title: res.msg,
							duration: 1500,
							icon: "none"
						});
						// console.log("保存失败");
					}
				},
				error: (res, httpStatus) => {
					console.log("接口调用失败，请检查网络");
				},
			});
		},
	}
}
